@import url(https://fonts.googleapis.com/css?family=Charm);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(left,#f100ff,#00cdff);
  background: linear-gradient(to right,#f100ff,#00cdff);
  background-size: cover;
  }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  


}
.fix
{
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

p
{
	font-family: 'Charm', cursive;
}
::-webkit-input-placeholder
{
	color: white;
	opacity: 0.7;
	text-align: center;
}
::-ms-input-placeholder
{
	color: white;
	opacity: 0.7;
	text-align: center;
}
::placeholder
{
	color: white;
	opacity: 0.7;
	text-align: center;
}
input
{
	color: white;
	padding-left: 15px;
	margin: 20px;
}

input:focus, textarea {
    outline: none !important;
}

#boundingbox
{
	position: absolute;
	border-style: solid;
	border-radius: 3px;
	border-width: 3px;
	border-color: blue;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	visibility: hidden;
}
.mt2
{
	text-align: center;
	display: inline-block;
}
